// Packages
import React from "react";
import { useNavigate } from "react-router-dom";

// Components

// Styles
import "../../styles/Packages/Card.scss";
import Button from "../Button";
import { HiOutlineArrowSmRight } from "../../Config/icons";

interface Props {
  type: string;
  background: string;
  title: string;
  slug?: string;
  price: string;
  currency: string;
  oldPrice: string;
  renewalPrice: string;
  features: any;
  badge?: boolean | null;
  isSub?: boolean;
}

function Card({ background, title, slug, price, currency, oldPrice, renewalPrice, features, type, badge = null, isSub = false }: Props) {
  const navigate = useNavigate();
  let currencySign = '';

  switch (currency) {
    case 'Dollar':
      currencySign = '$';
      break;
    case 'Euro':
      currencySign = '€';
      break;
    case 'Pound':
      currencySign = '£'
      break;
  }

  return (
    <div className="package">
      <header style={{ background: `linear-gradient(to bottom right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.7)), url(${(background)})` }}>
        <h4>{title}</h4>
        {badge && <span className="mark">{badge}</span>}
      </header>
      <section className="price-container">
        <div className="discount">
          <div className="oldPrice">{currencySign}{oldPrice}</div>
          <div className="percentage">%{Math.ceil((+price / +oldPrice) * 100)} OFF</div>
        </div>
        <span className="price">{currencySign}{price}</span>
        <p className="note">Renews at {renewalPrice}/year</p>
      </section>
      <section className="features">
        <ul>
          {features.map((feature: string, idx: number) => (
            <li key={idx}>
              <div className="arrow">
                <HiOutlineArrowSmRight />
              </div>
              <div className="feature">
                {feature}
              </div>
            </li>
          ))}
        </ul>
      </section>
      <section className="action">
        <Button onClick={() => navigate(`${isSub ? `/admin-orders/order/${type}/${slug}` : `/admin-orders/packages/${type}`}`)} style="primary">Learn More</Button>
      </section>
    </div>
  )
}

export default Card;