import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';

import Home from './Pages/Home';
import PackagesPage from './Pages/PackagesPage';
import CreateOrder from './Pages/CreateOrder/CreateOrder';
import axios from 'axios';

axios.defaults.baseURL = process.env.NODE_ENV === 'production' ? '/api' : 'http://localhost:8000/api';

function App() {
  return (
    <Router>
      <Routes>
        <Route path='/admin-orders' element={<Home />} />
        <Route path="/admin-orders/packages/:country" element={<PackagesPage />} />
          <Route path="/admin-orders/order/:country/:package" element={<CreateOrder />} />
      </Routes>
    </Router>
  );
}

export default App;
